@import './reset.scss';

$detailTColor : blue;

.blue{
    color : blue;
}

@mixin colorfunction() {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert{
    @include colorfunction();
}

.my-alert-2{
    @extend .my-alert;
    background-color: yellow;
}



.my-alert p{
    margin-bottom: 0;
}
